import logo from './logo.svg';
import './App.css';
import {
    MDBNavbar,
    MDBNavbarBrand
  } from "mdbreact";
import { Navbar, Nav } from 'react-bootstrap';

function Header() {
  return (
 
     
        <MDBNavbar  color="primary-dark"
              dark
              expand="md"
              fixed="top"
              scrolling
              transparent
              >
        <MDBNavbarBrand href="#home">
      <img
        src= "./logo192.png"
        width="50"
        height="50"
        className=" align-top"
        alt="logo"
        
      />
      </MDBNavbarBrand>
        <MDBNavbarBrand href="#home">Deepesh Patel</MDBNavbarBrand>
  
        <Nav className="ml-auto ">
        <Nav.Link activeclassName="menu-active " className="nav-link active" href="/">Home</Nav.Link>
        <Nav.Link activeclassName="menu-active" className="nav-link active" href="/Project">Projects</Nav.Link>
        <Nav.Link activeclassName="menu-active" className="nav-link active" href="/Profile">Profile</Nav.Link>
        <Nav.Link activeclassName="menu-active" className="nav-link active" href="/Certificate">Certificate</Nav.Link>
        <Nav.Link activeclassName="menu-active" className="nav-link active" href="/Experience">Experience</Nav.Link>
        <Nav.Link activeclassName="menu-active" className="nav-link active" href="/Education">Education</Nav.Link>

       
        </Nav>
    </MDBNavbar>
    

  );
}

export default Header;